        <p>
          在了解五十音之前，让我们先来我们平时认识的日语都由哪些东西构成，下面是一段摘抄:
        </p>
        <p class="card-extract">
          「今は新型コロナウイルス感染対策の正念場。そんな中、江蘇省蘇州市の中国庭園に住みついている
          猫たちも感染対策の最前線に駆り出された。猫たちは「ニャンコ版感染対策マニュアル」で、
          「外出時でも家にいる時でも、自分でできる感染対策を徹底してニャ！」と呼びかけている。」
        </p>
        <p>
          从这段话中我们可以找出有
          [汉字(暂且叫汉字)]、[不认识的日文]，仔细观察就会发现其中好像不认识的日文又有的圆圆的（如：そんな、みついている...）、有的笔锋比较硬朗（如：コロナウイルス），
          如果你发现了！！那么恭喜你！圆圆的大多都是平假名、硬硬的大多都是片假名，那么为什么平假名和片假名除了长的不一样以外，到底有什么不同？他们分别是用来干什么的呢？
          这里先记住一点：每个平假名都有对应的片假名，他们是各自一一对应的
